import React from "react";

const MoneyComponent: React.FC<{ date: string; value: number }> = (props) => {
	const date = new Date(props.date);
	return (
		<div>
			<table>
				<tr>
					<th>Date</th>
					<th>Value</th>
				</tr>
				<tr>
					<td>{`${date.getFullYear()}-${date.getMonth()}-${date.getDate()}`}</td>
					<td>{props.value}</td>
				</tr>
			</table>
		</div>
	);
};

export default MoneyComponent;
